<template>
  <div class="itInfo">
    <div v-if="type == 0" class="justify-around d-flex">
      <van-image
        :src="IMG + phoneButton"
        @click="getPhone(phone)"
        width="170px"
        height="36px"
        class="w-15"
      ></van-image>
      <van-image
        :src="IMG + addressButton"
        @click="getAddress(address, name, coordinate)"
        width="170px"
        height="36px"
      ></van-image>
    </div>
    <div v-else-if="type == 1" class="d-flex justify-between">
      <div
        class="d-flex button m-r-10"
        :style="{ 'background-color': backgroundColor }"
        @click="getPhone(phone)"
      >
        <van-image
          :src="IMG + phoneButton"
          :width="width"
          :height="height"
          class="m-r-10"
        ></van-image>
        拨打电话
      </div>
      <div
        class="d-flex button"
        :style="{ 'background-color': backgroundColor }"
        @click="getMap(address, name, coordinate)"
      >
        <van-image
          :src="IMG + addressButton"
          :width="width"
          :height="height"
          class="m-r-10"
        ></van-image>
        地址导航
      </div>
    </div>
    <!-- closeable close-icon-position="top-right" -->
    <van-popup
      position="bottom"
      v-model="phoneShow"
      :close-on-click-overlay="false"
      round
      :style="{ width: '100%', padding: '10px 0' }"
    >
      <div
        @click="getPhone2(item)"
        v-for="(item, index) in phoneList"
        :key="index"
        class="phoneText"
      >
        {{ item }}
      </div>

      <div @click="phoneShow = false" class="phoneText">取消</div>
    </van-popup>
    <van-popup
      position="bottom"
      v-model="addressShow"
      :close-on-click-overlay="false"
      round
      :style="{ width: '100%', padding: '10px 0' }"
    >
      <div
        @click="getAddress(item, name)"
        v-for="(item, index) in addressList"
        :key="index"
        class="addressText"
      >
        {{ item }}
      </div>

      <div @click="addressShow = false" class="addressText">取消</div>
    </van-popup>
  </div>
  <!-- position="bottom" -->
</template>

<script>
import { Toast } from "vant";
export default {
  props: {
    phoneButton: String,
    addressButton: String,
    phone: String,
    address: String,
    coordinate: String,
    name: {
      type: String,
      default: "正在加载",
    },
    type: {
      type: Number,
      default: 0, ///0默认按钮图片  1两个icon
    },
    width: {
      type: String,
      default: "16px",
    },
    height: {
      type: String,
      default: "16px",
    },
    backgroundColor: {
      type: String,
      default: "#d0333a",
    },
    phoneName: {
      type: String,
      default: null,
    },
    addressName: {
      type: String,
      default: null,
    },
    // isTap:{
    //   type:Boolean,
    //   default:true
    // }
  },
  data() {
    return {
      IMG: this.IMG,
      phoneShow: false,
      phoneList: [],
      addressList: [],
      addressShow: false,
    };
  },
  methods: {
    getPhone2(phone) {
      console.log(phone);
      this.phoneShow = false;
      window.location.href = "tel:" + phone;
    },
    // 电话
    getPhone(phone) {
      console.log("拨打电话", this.phoneName);
      if (this.phoneName) {
        if (phone) {
          this.phoneShow = true;
          this.phoneList = phone.split(this.phoneName);
        } else {
          Toast.fail("暂无数据");
        }
      } else {
        window.location.href = "tel:" + phone;
      }
    },
    // 地址
    getMap(address, name, coordinate) {
      if (this.addressName) {
        if (address) {
          this.addressShow = true;
          this.addressList = address.split(this.addressName);
        } else {
          Toast.fail("暂无数据");
        }
      } else {
        this.getAddress(address, name, coordinate);
      }
    },
    getAddress(address, name, coordinate) {
      console.log(address, name, coordinate);

      Toast.loading({
        message: "加载中...",
        forbidClick: true,
      });
      this.addressShow = false;
      this.$router.push({
        path: "/map",
        query: {
          address: address,
          name: name,
          isTap: true,
          coordinate: coordinate,
        },
      });
      Toast.clear;
    },
  },
};
</script>

<style lang="scss" scoped>
.itInfo {
  justify-content: space-between;
  display: flex;
  width: 100%;
  height: 66px;
  background: #ffffff;
  border-radius: 8px;
  opacity: 1;
  margin: 0 auto;
  margin: 6px 0;
  padding: 15px 12px;
  box-sizing: border-box;

  image {
    height: 100%;
  }
}
.w-15 {
  margin-right: 15px;
}
.button {
  background-color: #d0333a;
  color: #ffffff;
  text-align: center;
  font-size: 14px;
  padding: 8px 0;
  border-radius: 8px;
  white-space: nowrap;
  width: 155px;
  justify-content: center;
}
.justify-around {
  justify-content: space-around;
  width: 100%;
}
.justify-between {
  justify-content: space-between;
  width: 100%;
}
.m-r-10 {
  margin-right: 10px;
}
.phoneText {
  color: #333;
  font-size: 14px;
  margin: 10px 0;
  text-align: center;
  height: 30px;
  // white-space: nowrap; /* 防止文本换行 */
  // overflow: hidden; /* 隐藏超出容器的内容 */
  // text-overflow: ellipsis; /* 使用省略号表示被截断的内容 */
}
.addressText {
  color: #333;
  font-size: 14px;
  margin: 10px 0;
  text-align: center;
  height: 30px;
  white-space: nowrap; /* 防止文本换行 */
  overflow: hidden; /* 隐藏超出容器的内容 */
  text-overflow: ellipsis; /* 使用省略号表示被截断的内容 */
}
</style>



