<template>
  <div>
    <div v-if="styleType === 0">
      <van-image :src="IMG + 'yt/image/jjBg.png'" class="m-t-14" />
        <p
          class="textStyle"
          v-for="(item, index) in text.split(' ')"
          :key="index"
           style="line-height:20px;letter-spacing:1px;"
        >
          {{ item }}
        </p>
    </div>

    <div v-else-if="styleType === 1">
      <i-text
      v-if="!isHome"
        style="margin-top: 10px"
        :iconImageCss="{ width: '5px', height: '5px' }"
        iconImage="yt/icon/round.png"
        :title="styleData.name"
      ></i-text>
      <div class="ys-box">
        <div
          v-for="(item, index) in styleData.solution_list"
          :key="index"
          class="ys-item"
        >
          <img :src="item.image.url" />
          <span class="name">{{ item.name }}</span>
          <span class="describe">{{ item.describe }}</span>
        </div>
      </div>
    </div>

    <div v-else-if="styleType === 2">
      <i-text
        style="margin-top: 10px"
        :iconImageCss="{ width: '5px', height: '5px' }"
        iconImage="yt/icon/round.png"
        :title="styleData.name"
      ></i-text>
      <div v-for="(item, index) in styleData.solution_list" :key="index">
        <i-text
          style="margin-bottom: 6px; margin-top: 10px"
          :iconImageCss="{ width: '13px', height: '13px' }"
          :iconImage="item.image.url"
          :title="item.name"
        ></i-text>
        <div class="wh-describe">{{ item.describe }}</div>
      </div>
    </div>

    <div v-else-if="styleType === 3">
      <i-text
        :iconImageCss="{ width: '5px', height: '5px' }"
        iconImage="yt/icon/round.png"
        :title="styleData.name"
      ></i-text>
      <van-row :gutter="10" v-for="(row, rowIndex) in rows" :key="rowIndex">
        <van-col
          v-for="(item, index) in row"
          :key="index"
          :span="24 / row.length"
        >
          <img :src="item.image.url" class="image-item" />
        </van-col>
      </van-row>
    </div>

    <div v-else-if="styleType === 4">
      <i-text
        :iconImageCss="{ width: '5px', height: '5px' }"
        iconImage="yt/icon/round.png"
        :title="styleData.name"
      ></i-text>
      <div v-for="(item, index) in styleData.solution_list" :key="index">
        <img :src="item.image.url" width="100%" />
      </div>
    </div>

    <div v-else-if="styleType === 5">
      <i-text
        style="margin-top: 10px"
        :iconImageCss="{ width: '5px', height: '5px' }"
        iconImage="yt/icon/round.png"
        :title="styleData.name"
      ></i-text>
      <div
        v-for="(group, groupIndex) in groupedImages"
        :key="groupIndex"
        class="image-group"
      >
        <div class="row">
          <div class="image-left" v-if="group[0].image">
            <img :src="group[0].image.url" class="image-item" />
          </div>
          <div class="image-right">
            <div class="image-item" v-if="group[1].image">
              <img :src="group[1].image.url" />
            </div>
            <div class="image-item" v-if="group[2].image">
              <img :src="group[2].image.url" />
            </div>
          </div>
        </div>

        <div class="row" style=" flex-direction: column;">
          <div class="image-center" v-if="group[3].image">
            <img :src="group[3].image.url" class="image-item" />
          </div>
          <div class="image-center" style="margin-bottom: 8px;margin-top:5px" v-if="group[4].image">
            <img :src="group[4].image.url" class="image-item" />
          </div>
        </div>

        <div
          class="row"
          style="
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 10px;
          "
        >
          <div class="image-item" v-if="group[5].image">
            <img :src="group[5].image.url" />
          </div>
          <div class="image-item" v-if="group[6].image">
            <img :src="group[6].image.url" />
          </div>
        </div>
        <div class="row" style="margin-bottom: 8px">
          <div class="image-center" v-if="group[7].image">
            <img :src="group[7].image.url" class="image-item" />
          </div>
        
        </div>
      </div>
    </div>

    <div v-else>
      <!-- Default content or empty -->
    </div>
  </div>
</template>

<script>
import iText from "../tool/iText.vue";

export default {
  components: { iText },
  props: {
    styleType: {
      type: Number,
      default: 0,
    },
    styleData: {
      type: Object,
      default: () => ({}),
    },
    isHome: {
      type: Boolean,
      default: false,
    },
  },
  data(){
    return{
      text: '易通科技物流集团创立于2012年，注册资金2000万元，总部位于粤港澳大湾区核心城市——深圳，公司依托工程项目，在香港、上海、宁波、嘉兴、无锡、福州、厦门、中山、东莞、天津、青岛、大连等国内主要口岸城市均设有分公司；为了完善全球网络布局，为客户提供更优质的物流服务，公司在美国洛杉矶，新加坡，越南胡志明，意大利米兰均设有子公司(办事处);为客户提供海运进出口，空运进出口，大件运输等物流服务。 依靠强大的专业团队以及仓储资源，公司自2016年开始进军亚马逊FBA电商物流，先后在上海、东莞、义乌、宁波设立仓储中心，并在美国、意大利设立海外分拨基地。目前FBA电商物流运输范围涵盖“美国、加拿大、欧洲全境"等多个国家，可为客户提供美森快船、COSCO快船、长荣定提、以星快船、空派、中欧铁路运输、国际快递等全方位的亚马逊物流服务。 自营美西、美东海外仓，配备专业的华人操作团队，致力于为TK、TEMU、EBAY、速卖通等平台的卖家提供一件代发的卓越服务。我们承诺在24小时内完成操作指令，确保48小时内快递即可提取派送。此外，我们还提供移仓换标、大货中转、退货维修等一系列全面的库内操作服务，致力于为你打造全方位的物流服务，让您的跨境贸易更加顺畅无忧。 易通科技，引领全球。易通全体同仁将始终秉持创新、拼搏、共赢的企业精神，让每一位客户都能乐享快捷、安全的优质服务。',

    }
  },
  computed: {
    rows() {
      console.log("this.styleType", this.styleType);
      if (this.styleType !== 3) return [];
      const images = this.styleData.solution_list; // 获取图片列表

      // 初始化存储每行图片的数组
      let rows = [];
      let rowSize = 2; // 第一行显示 2 张图片

      for (let i = 0; i < images.length; ) {
        if (rows.length === 1) {
          // 第二行开始，显示 3 张图片
          rowSize = 3;
        }

        // 将当前的图片分配到这一行
        rows.push(images.slice(i, i + rowSize));

        // 更新 i 索引
        i += rowSize;
      }

      return rows;
    },
    groupedImages() {
      console.log("123123123", this.styleType);
      if (this.styleType !== 5) return [];
      console.log(
        "111111111111111111",
        this.chunkArray(this.styleData.solution_list, 8)
      );
      return this.chunkArray(this.styleData.solution_list, 8);
    },
  },
  methods: {
    processHtmlContent(html) {
      if (typeof html !== "string") return "";

      let processedHtml = html
        // **确保 <p> 标签包含 <img> 时不修改样式**
        .replace(/<p(?![^<]*<img)(.*?)>/g, (match, p1) => {
          return `<p${p1} style="line-height:20px;text-indent:0.5em;width:100%;height:100%;text-align: justify;">`;
        })

        // **确保 <span> 标签拥有正确的样式**
        .replace(/<span([^>]*)>/g, (match, p1) => {
          return `<span${p1} style="line-height:0.6;text-indent:0.5em;">`;
        })

        // **删除 <br> 标签**
        .replace(/<br\s*\/?>/gi, "")

        // **修正已有的 line-height 样式**
        .replace(
          /(style=['"][^'"]*?)line-height:[^;'"]*?([^'"]*['"])/g,
          (match, p1, p2) => `${p1}line-height:0.6;text-indent:0.5em;${p2}`
        );

      // **如果是首页，进行额外的处理**
      if (this.isHome) {
        let count = 0;
        processedHtml = processedHtml.replace(
          /<p[^>]*>\s*<span[^>]*>.*?<\/span>\s*<\/p>/g,
          (match) => {
            count++;
            return count === 1 ? match : "";
          }
        );

        console.log("processedHtml", processedHtml);
      }

      return processedHtml;
    },
    chunkArray(arr, size) {
      const result = [];
      for (let i = 0; i < arr.length; i += size) {
        result.push(arr.slice(i, i + size));
      }
      return result;
    },
  },
};
</script>

<style lang="scss" scoped>
.ys-box {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin: 5px 0;
}

.ys-item {
  border-radius: 8px;
  background: rgba(0, 64, 151, 0.05);
  display: flex;
  flex-direction: column;
  padding: 5px;
  align-items: center;
}

.ys-item img {
  width: 21px;
  height: 21px;
}

.ys-item .name {
  font-size: 14px;
  color: #333;
  line-height: 20px;
  font-weight: 400;
  align-items: center;
}

.ys-item .describe {
  font-size: 10px;
  color: #666;
  text-align: center;
}
.wh-describe {
  color: #666666;
  font-size: 12px;
  margin: 0 0 5px 20px;
}
.image-group {
  margin-bottom: 20px;
}

.row {
  display: flex;
  justify-content: space-between;
}

.image-left,
.image-right,
.image-center {
  display: flex;
  flex-direction: column;
}

.image-left {
  width: 48%;
}

.image-right {
  width: 48%;
  display: flex;
  flex-direction: column;
}

.image-center {
  width: 100%;
  justify-content: center;
}

.image-item {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.image-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  object-fit: cover;
}
</style>



