<template>
  <div class="d-flex m-b-10 fonts" :class="{ 'a-baseline': title.length > 16 }">
    <van-image
      v-if="titles == ''"
      class="iconImage"
      :src="iconImage.startsWith('https://') ? iconImage : IMG + iconImage"
      :width="iconImageCss.width"
      :height="iconImageCss.height"
    />
    <div class="font2" :style="{'font-size': fontSize2 }" v-else>{{ titles }}</div>
    <div class="font1" :style="{ color: color, 'font-size': fontSize }">
      {{ title }}
    </div>
  </div>
</template>

<script>
export default {
  props: {
    iconImage: String,
    iconImageCss: {
      type: Object,
      default: function () {
        return {
          width: "12px",
          height: "12px",
        };
      },
    },
    title: String,
    titles: {
      type: String,
      default: "",
    },
    color: {
      type: String,
      default: "#000",
    },
    fontSize: {
      type: String,
      default: "12px",
    },
    fontSize2: {
      type: String,
      default: "14px",
    },
  },
  data() {
    return {
      IMG: this.IMG,
    };
  },
};
</script>

<style lang="scss" scoped>
// .fonts{

// }
.a-baseline {
  align-items: baseline;
}
.iconImage {
  width: 12px;
  height: 12px;
  margin-right: 5px;
}
.font1 {
  font-size: 12px;
  color: #000;
  // width: 308px;
}
.font2 {
  // font-size: 14px;
  color: #333;
  white-space: nowrap;
}
.m-b-10 {
  margin-bottom: 10px;
}
</style>



