<template>
  <van-popup
    v-model="show"
    closeable
    close-icon-position="top-right"
    round
    :style="customStyle"
  >
    <div
      class="popupText"
      v-for="item in nameList"
      :key="item.type"
      @click="getClick(item.type)"
    >
      {{ item.name }}
    </div>
  </van-popup>
</template>

<script>
import { Toast } from "vant";
export default {
  props: {
    nameList: {
      type: Array,
      default: () => [
        { name: "拨打电话", type: "phone" },
        { name: "定位导航", type: "address" },
      ],
    },
    customStyle: {
      type: Object,
      default: () => ({
        width: "50%",
        "padding-top": "30px",
      }),
    },
    data: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      show: false,
    };
  },
  methods: {
    getClick(type) {
      if (!this.data.phone && !this.data.address) {
        Toast.fail("缺少参数");
        return;
      }

      switch (type) {
        case "phone":
          if (this.data.phone) {
            window.location.href = `tel:${this.data.phone}`;
          } else {
            Toast.fail("电话号码不可用");
          }
          break;

        case "address":
          if (this.data.address) {
            Toast.loading({ message: "加载中...", forbidClick: true });

            this.$router.push({
              path: "/map",
              query: {
                address: this.data.address,
                name: this.data.addressName || "未知位置",
                isTap: true,
              },
            }).finally(() => {
              Toast.clear(); // 这里在 push 结束后清除 loading
            });
          } else {
            Toast.fail("地址不可用");
          }
          break;

        default:
          Toast.fail("无效的操作类型");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.popupText {
  font-size: 14px;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #f6f6f6;
  cursor: pointer; /* 增强交互性 */
  transition: background 0.2s;

  &:hover {
    background: #f0f0f0;
  }
}
</style>



