<template>
  <div>
    <headerContent
      :width="
        name == '公司简介'
          ? '94px'
          : name == '资质证书'
          ? '136px'
          : name == '合作伙伴' || name == '全部合作伙伴'
          ? '97px'
          : name == '产品展示'
          ? '90px'
          : name == '心研优势'
          ? '136px'
          : '74px'
      "
      height="48px"
      :headerImg="
        name == '公司简介'
          ? 'xy/headerImage/gsjj.png'
          : name == '资质证书'
          ? 'xy/headerImage/zzzs.png'
          : name == '合作伙伴' || name == '全部合作伙伴'
          ? 'xy/headerImage/hzhb.png'
          : name == '产品展示'
          ? 'xy/headerImage/cpzs.png'
          : name == '心研优势'
          ? 'xy/headerImage/xyys.png'
          : 'xy/headerImage/lxwm.png'
      "
    >
      <template #content>
        <div v-if="name == '公司简介'" id="gsjs">
          <div class="h-10"></div>
          <van-image :src="IMG + 'xy/image/jsBg.png'"></van-image>
          <div class="textStyle" v-for="(item, index) in text.split(' ')">
            {{ item }}
          </div>
          <more
            color="#666666"
            title="查看更多"
            path="/thirdIndex"
            float="flex-start"
          ></more>
        </div>
        <div v-if="name == '资质证书'">
          <div v-for="(item, index) in getContent('资质证书')" :key="index">
            <div class="zzName">{{ item.name }}</div>
            <van-image
              class="zzImg"
              v-if="item.image"
              :src="item.image.url"
            ></van-image>
          </div>
        </div>
        <div v-if="name == '合作伙伴' || name == '全部合作伙伴'">
          <div class="hbBox">
            <van-image
              :src="item.image.url"
              :key="index"
              v-for="(item, index) in getContent('合作伙伴')"
              width="102px"
              height="48px"
              v-if="
                item.image && name == '全部合作伙伴'
                  ? index <= getContent('合作伙伴').length - 1
                  : index < 9
              "
            ></van-image>
          </div>
          <div class="h-10"></div>
          <more
            v-if="name != '全部合作伙伴'"
            title="查看更多"
            path="/thirdIndex"
            float="flex-start"
            color="#666666"
          ></more>
        </div>
        <div v-if="name == '产品展示' || name == '全部产品展示'">
          <div class="cpBox">
            <div
              v-for="(item, index) in getContent('产品展示')"
              :key="index"
              class="cpItem"
              @click="getTo(item.id)"
              v-if="
                name == '全部产品展示'
                  ? index <= getContent('产品展示').length - 1
                  : index < 4
              "
            >
              <van-image
                class="img"
                :src="item.image.url"
                width="100%"
                height="103px"
                v-if="item.image"
              />
              <div class="name">{{ item.name }}</div>
            </div>
          </div>
          <div class="h-10"></div>
          <more
            v-if="name != '全部产品展示'"
            title="查看更多"
            path="/secondIndex"
            float="flex-start"
            color="#666666"
          ></more>
        </div>
        <div v-if="name == '心研优势'" class="ysBox">
          <!-- <div class="h-10"></div> -->
          <div
            v-for="(item, index) in getContent('心研优势')"
            :key="index"
            class="ysItem"
          >
            <van-image :src="item.image.url" width="44px" height="44px" />
            <div class="name">{{ item.name }}</div>
            <div class="name_en">{{ item.name_en }}</div>
            <span class="describe">{{ item.describe }}</span>
          </div>
        </div>
        <div v-if="name == '联系我们'" id="lxwm">
          <div class="h-10"></div>
          <companyTitle
            title="深圳市心研医疗科技有限公司"
            fontSize="15px"
          ></companyTitle>
          <iText
            titles="联系电话："
            title="18098974561"
            color="#666666"
            fontSize="14px"
          ></iText>
          <iText
            titles="电子邮箱："
            title="xinyancare@126.com"
            color="#666666"
            fontSize="14px"
          ></iText>
          <iText
            titles="公司地址："
            title="深圳市坪山区锦绣中路9号米特华浦京生物科学园301-7"
            color="#666666"
            fontSize="14px"
          ></iText>
          <TXMap
            mapWidth="100%"
            mapHeight="130px"
            address="深圳市坪山区锦绣中路9号米特华浦京生物科学园301-7"
            name="心研医疗科技"
          ></TXMap>
        </div>
      </template>
    </headerContent>
  </div>
</template>

<script>
import companyTitle from "../tool/companyTitle.vue";
import IText from "../tool/iText.vue";
import More from "../tool/more.vue";
export default {
  components: { companyTitle, More, IText },
  props: {
    name: {
      type: String,
      default: "",
    },
    list: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      show: false,
      phoneAndAddress: {
        name: "",
        pdf: "",
        name_en: "",
        describe: "",
      },
      text: "公司总部位于浙江宁波，研发中心位于宁波和深圳，生产基地覆盖安徽省、广东省、上海市、陕西省、四川省、浙江省等地。 公司汇聚了一批国内外顶级心脑；高端芯片；Al人工智能；硬件研发等专家团队，是中国领先的医疗解决方案供应商。心研医疗结合自身技术优势，在低功耗硬件心电模块、心电算法、血氧算法领域持续创新。目前，心研医疗产品解决方案已服务于医院、消费市场，为客户提供标准化，定制化等专业服务。",
    };
  },
  methods: {
    getTo(id) {
      this.$router.push({
        path: "/categorySolutionInfo",
        query: { id: id },
      });
    },
    butTab(id) {
      console.log("硕为思", id);
      document.getElementById(id).scrollIntoView();
    },
    // 电话
    getPhone() {
      window.location.href = "tel:" + this.phoneAndAddress.describe;
    },
    // 地址
    getAddress() {
      Toast.loading({
        message: "加载中...",
        forbidClick: true,
      });
      this.$router.push({
        path: "/map",
        query: {
          address: this.phoneAndAddress.pdf,
          name: this.phoneAndAddress.name_en,
          isTap: true,
        },
      });
      Toast.clear;
    },
    getShow(res) {
      this.show = true;
      this.phoneAndAddress = res;
    },
    getUrl(str) {
      return "'" + str + "'";
    },
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
  },
};
</script>

<style lang="scss" scoped>
.h-10 {
  height: 10px;
}
.zzName {
  color: #333;
  font-size: 12px;
  margin: 8px;
  font-weight: 600;
}
.zzImg {
  display: flex;
  margin: auto;
}
.hbBox {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  column-gap: 10px;
  row-gap: 10px;
  margin-top: 10px;
}
.cpBox {
  display: grid;
  grid-template-columns: 49% 49%;
  column-gap: 10px;
  row-gap: 10px;
  margin-top: 10px;
  .cpItem {
    // width: fit-content;
    border-radius: 4px;
    background-color: #fff;
    // padding-bottom: 8px;
    box-shadow: 0px 2px 8px 0px rgba(1, 106, 66, 0.08);
    text-align: center;
  }
  .name {
    font-size: 13px;
    color: #333;
    margin: 8px 0;
  }
  .img {
  }
}
.ysBox {
  display: grid;
  grid-template-columns: 49% 49%;
  column-gap: 10px;
  row-gap: 10px;
  margin-top: 10px;
  .ysItem {
    background-color: #f6f6f6;
    border-radius: 4px;
    padding: 12px;
    text-align: center;
    display: flex;
    flex-direction: column;
    /deep/ .van-image {
      display: flex;
      margin: auto;
    }
    // margin: auto;
  }
  .name {
    color: #333333;
    font-size: 13px;
    margin: 5px 0 0 0;
  }
  .name_en {
    color: #d62223;
    font-size: 10px;
    margin: 5px 0;
  }
  .describe {
    color: #666666;
    font-size: 10px;
  }
}
</style>



