<template>
  <div>
    <headerContent
      headerImg="ddzn/headerImage/qyjs.png"
      width="126px"
      height="48px"
      v-if="isFirst"
      id="qyjs"
    >
      <template #content>
        <div class="h-10"></div>
        <van-image :src="IMG + 'ddzn/image/jsBg.png'"></van-image>

        <div class="textStyle" v-for="(item, index) in text.split(' ')">
          {{ item }}
        </div>
        <div class="h-10"></div>
        <more
          title="查看更多"
          icon="arrow"
          path="/fourthIndex"
          color="#666666"
          float="flex-start"
        ></more>
      </template>
    </headerContent>
    <headerContent
      headerImg="ddzn/headerImage/wmdys.png"
      width="85px"
      height="48px"
    >
      <template #content>
        <div
          class="ysBox"
          :style="{ 'background-image': 'url(' + getUrl(item.image.url) + ')' }"
          v-if="item.image"
          v-for="(item, index) in getContent('我们的优势')"
        >
          <div class="name">{{ item.name }}</div>
          <div class="describe">{{ item.describe }}</div>
        </div>
      </template>
    </headerContent>
    <headerContent
      headerImg="ddzn/headerImage/hzkh.png"
      width="126px"
      height="48px"
    >
      <template #content>
        <div class="hzBox">
          <van-image
            v-if="item.image"
            v-for="(item, index) in getContent('合作客户')"
            :key="index"
            width="102px"
            height="48px"
            :src="item.image.url"
          ></van-image>
        </div>
      </template>
    </headerContent>
    <headerContent
      headerImg="ddzn/headerImage/fwxm.png"
      width="74px"
      height="48px"
      id="fwxm"
      v-if="isFirst"
    >

      <template #content>
        <div
          class="fwBox"
          v-if="item.image && index<=2"
          :style="{ 'background-image': 'url(' + getUrl(item.image.url) + ')' }"
          v-for="(item, index) in getContent('服务项目')"
          :key="index"
          @click="getTo(item.id)"
        >
          <div class="fwItem">
            <div class="name">{{ item.name }}</div>
            <div class="nameEn">{{ item.describe }}</div>

            <van-image width="29px" height="5px" :src="IMG + 'ddzn/icon/right.png'"></van-image>
          </div>
        </div>
        <div class="h-10"></div>
        <more
          title="查看更多"
          icon="arrow"
          path="/secondIndex"
          color="#666666"
          float="flex-start"
        ></more>
      </template>
    </headerContent>
    <headerContent
      headerImg="ddzn/headerImage/lxwm.png"
      width="74px"
      height="48px"
      id="lxwm"

    >
      <template #content>
        <company-title title="宁波叮当智能科技有限公司"
            fontSize="15px">  </company-title>
       <i-text titles="电话：" title="0574-87713657" color="#666666" fontSize="14px"></i-text>
       <i-text titles="邮箱：" title="office@akuhome.com" color="#666666" fontSize="14px"></i-text>
       <i-text titles="地址：" title="浙江省宁波市高新区深蓝大厦503" color="#666666" fontSize="14px"></i-text>
        <div class="h-10"></div>
        <TXMap
          mapWidth="100%"
          mapHeight="130px"
          address="浙江省宁波市高新区深蓝大厦503"
          name="叮当智能"
        ></TXMap>
      </template>
    </headerContent>
  </div>
</template>

<script>
import headerContent from "../tool/headerContent.vue";
export default {
  components: { headerContent },
  props: {
    isFirst: {
      type: Boolean,
      default: true,
    },
    list: {
      type: Array,
      default: [],
    },
  },
  data(){
    return{
      text:'宁波叮当智能科技有限公司于2017年创办于宁波，专注于为企业提供研究开发、产学研合作、技术转移、方案设计、产品认证、检测检验、创业孵化、知识产权、科技咨询、项目申报、财务咨询、财务审计、资产评估、科学技术普及等“一站式”科创服务。 我们现以“诚信、敬业、成就客户”为服务理念不断通过创新服务提升客户的满意度，以“以创新为动力、以技术为支撑、以客户为中心”的发展愿景，通过与各地高新区、开发区、科技园管委会等政府科技服务机构的密切沟通，重构完善了知识产权服务及科技服务体系。公司已与国内众多知名第三方机构、实验室、高校院所等保持良好的合作关系，已整合更有效的资源，为客户提供优质的科技创新服务。'
    }
  },
  methods:{
    butTab(id) {
      document.getElementById(id).scrollIntoView();
    },
    getTo(id){
      this.$router.push({
        path:'/categorySolutionInfo',
        query: { id: id },
      })
    },
    getContent(name){
      let data=this.list.find((item)=>item.name==name)
      return data?data.solution_list:[]
    },
    getUrl(str){
        return "'" +str +"'"
    }
  }
};
</script>

<style lang="scss" scoped>
.h-10 {
  height: 10px;
}
.ysBox {
  width: 100%;
  height: 64px;
  padding: 14px 47px;
  margin-top: 12px;
  background-size: 100% 100%;
  .name {
    color: #333333;
    font-size: 13px;
    line-height: 19px;
  }
  .describe {
    color: #666666;
    font-size: 10px;
    line-height: 15px;
  }
}
.hzBox {
  display: grid;
  grid-template-columns: 32% 32% 32%;
  column-gap: 10px;
  row-gap: 10px;
  margin-top: 10px;
}
.fwBox {
  width: 319px;
  height: 160px;
  background-size: 100% 100%;
  display: flex;
  margin-top: 12px;
  border-radius: 8px;
  justify-content: flex-end;
  flex-direction: column;
  .fwItem {
    height: 63px;
    padding: 12px;
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    border-end-end-radius: 9px;
    border-end-start-radius: 9px;

    .name {
      color: #ffffff;
      font-size: 15px;
      font-weight: 500;
    }

    .nameEn {
      width: 290px;
      color: #ffffff;
      font-size: 10px;
      font-weight: 400;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      margin-bottom: 5px;
    }
  }
}
</style>



