<template>
  <div>
    <headerContent
      :headerImg="
        name == '公司介绍'
          ? 'sws/headerImage/gsjs.png'
          : name == '业务范畴'
          ? 'sws/headerImage/ywfc.png'
          : name == '我们的荣誉'
          ? 'sws/headerImage/wmdry.png'
          : name == '我们的客户'
          ? 'sws/headerImage/wmdkh.png'
          : name == '我们的优势'
          ? 'sws/headerImage/wmdys.png'
          : name == '案例'
          ? 'sws/headerImage/al.png'
          : 'sws/headerImage/lxwm.png'
      "
    >
      <template #content>
        <div v-if="name == '公司介绍'" id="gsjs">
          <div class="h-10"></div>
          <van-image :src="IMG + 'sws/image/jsBg.png'"></van-image>
          <company-title
            title="硕而有为，集优而思——创立于2006年"
            color="#EB6027"
            fontSize="14px"
          ></company-title>
          <div class="d-flex jsTitle">
            <div class="circular"></div>
            我们是一家技术型的公关公司
          </div>
          <div class="jsText">
            国家高新技术企业与中关村高新技术企业，中国商务广告协会数字营销委员理事会成员单位，并与北航数据智能研究中心达成深度战略合作，自有多项软著和独有行业数据
          </div>
          <div class="d-flex jsTitle">
            <div class="circular"></div>
            我们专注于汽车领域
          </div>
          <div class="jsText">
            十余年来深耕汽车领域，专注于用户互动生态建设、新媒体内容营销、舆情监测及口碑管理、搜索环境优化及大数据等服务，一直致力于为国内外优秀的品牌和企业提供精细化的数字营销服务
          </div>

          <div class="d-flex jsTitle">
            <div class="circular"></div>
            我们全产业链服务
          </div>
          <div class="jsText">
            目前拥有超300人的专业服务团队，在北京、上海、广州、深圳及杭州设有分公司，在山东及山西建有两个产业基地，实现前-中-后端全产业链化协同作业，可快速反应高效运转
          </div>
        </div>
        <div v-if="name == '业务范畴'">
          <div class="h-10"></div>
          <div class="yw-box">
            <div
              v-for="(item3, index3) in getContent('业务范畴')"
              v-if="item3.image"
              :key="index3"
              class="item"
            >
              <img :src="item3.image.url" />
              <span>{{ item3.name }}</span>
            </div>
          </div>
        </div>
        <div v-if="name == '我们的荣誉'">
          <div class="h-10"></div>
          <div class="ry-box1">
            <div
              class="item"
              v-if="item4.image"
              :key="index4"
              v-for="(item4, index4) in getContent('我们的荣誉')"
            >
              <img :src="item4.image.url" />
              <span>{{ item4.name }}</span>
            </div>
          </div>
          <img :src="IMG + 'sws/image/ry4.png'" style="width:100%" />
          <span
            style="font-size:13px;color:#000;display: block;line-height:16px;text-align: center;"
            >超过40多名技术精英，通过研究汽车行业特点，自主研发12个国家专利行业应用技术</span
          >

        </div>
        <div v-if="name == '我们的客户'" class="khBox">
          <van-image
            :src="item2.image.url"
            v-if="item2.image"
            :key="index2"
            v-for="(item2, index2) in getContent('我们的客户')"
            width="100px"
            height="60px"
          ></van-image>
        </div>
        <div v-if="name == '我们的优势'" class="ysBox">
          <div
            v-for="(item2, index2) in getContent('我们的优势')"
            :key="index2"
            class="ysItem"
          >
            <van-image :src="item2.image.url" width="20px" height="20px" />
            <div class="name">{{ item2.name }}</div>
            <span class="describe">{{ item2.describe }}</span>
          </div>
        </div>
        <div v-if="name == '案例' || name == '全部案例'" id="al">
          <div
            v-for="(item2, index2) in getContent('案例')"
            :style="{
              'background-image': 'url(' + getUrl(item2.image.url) + ')',
            }"
            class="alImage"
            @click="getTo(item2.id)"
            v-if="
              name == '全部案例'
                ? index2 <= getContent('案例').length - 1
                : index2 < 3
            "
            :key="index2"
          >
            <div class="alName">{{ item2.name }}</div>
          </div>
          <div class="h-10"></div>
          <more
            v-if="name == '案例'"
            title="查看更多方案"
            path="/secondIndex"
            float="flex-start"
          ></more>
        </div>
        <div v-if="name == '联系我们' || name == '全部联系我们'" id="lxwm">
          <div
            class="lxBox"
            v-for="(item2, index2) in getContent('联系我们')"
            :key="index2"
            v-if="
              name == '全部联系我们'
                ? index2 <= getContent('联系我们').length - 1
                : index2 < 3
            "
            @click="getShow(item2)"
          >
            <div class="d-flex name">
              <div class="line"></div>
              {{ item2.name }}
            </div>
            <i-text
              iconImage="sws/image/company.png"
              :title="item2.name_en"
            ></i-text>
            <i-text iconImage="sws/image/phone.png" :title="item2.pdf"></i-text>
            <i-text
              iconImage="sws/image/address.png"
              :title="item2.describe"
            ></i-text>
          </div>
          <div class="h-10"></div>
          <more
            v-if="name != '全部联系我们'"
            title="更多联系方式"
            path="/fourthIndex"
            float="flex-start"
          ></more>
        </div>
      </template>
    </headerContent>
    <van-popup
      v-model="show"
      closeable
      close-icon-position="top-right"
      round
      :style="{ width: '50%', 'padding-top': '30px' }"
    >
      <!-- <div class="popupText" >{{ phoneAndAddress.name || '加载中' }}</div> -->
      <div class="popupText" @click="getPhone">拨打电话</div>
      <div class="popupText" @click="getAddress">定位导航</div>
    </van-popup>
  </div>
</template>

<script>
import companyTitle from "../tool/companyTitle.vue";
import IText from "../tool/iText.vue";
import More from "../tool/more.vue";
import { Toast } from "vant";
export default {
  components: { companyTitle, More, IText },
  props: {
    name: {
      type: String,
      default: "",
    },
    list: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      show: false,
      phoneAndAddress: {
        name: "",
        pdf: "",
        name_en: "",
        describe: "",
      },
    };
  },
  methods: {
    getTo(id){
    this.$router.push({
        path:'/categorySolutionInfo',
        query: { id: id },
      })
  },
    butTab(id) {
      console.log("硕为思", id);
      document.getElementById(id).scrollIntoView();
    },
    // 电话
    getPhone() {
      window.location.href = "tel:" + this.phoneAndAddress.pdf;
    },
    // 地址
    getAddress() {
      Toast.loading({
        message: "加载中...",
        forbidClick: true,
      });
      this.$router.push({
        path: "/map",
        query: {
          address: this.phoneAndAddress.describe,
          name: this.phoneAndAddress.name_en,
          isTap: true,
        },
      });
      Toast.clear;
    },
    getShow(res) {
      this.show = true;
      this.phoneAndAddress = res;
    },
    getUrl(str) {
      return "'" + str + "'";
    },
    getContent(name) {
      const data = this.list.find((item) => item.name == name);
      return data ? data.solution_list : [];
    },
  },
};
</script>

<style lang="scss" scoped>
.circular {
  background-color: #eb6027;
  border-radius: 3px;
  margin-right: 5px;
  width: 6px;
  height: 6px;
}
.jsTitle {
  color: #333333;
  font-size: 12px;
  font-weight: 800;
}
.jsText {
  font-size: 12px;
  color: #666;
  font-weight: 400;
  line-height: 20px;
  margin: 5px;
}
.khBox {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}
.ysBox {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-top: 10px;
  .ysItem {
    background-color: #f6f6f6;
    border-radius: 4px;
    padding: 12px;
    text-align: center;
    display: flex;
    flex-direction: column;
    /deep/ .van-image {
      display: flex;
      margin: auto;
    }
    // margin: auto;
  }
  .name {
    color: #333333;
    font-size: 14px;
    margin: 5px;
  }
  .describe {
    color: #666666;
    font-size: 10px;
  }
}
.alImage {
  width: 100%;
  height: 180px;
  background-size: 100% 100%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.alName {
  color: #fff;
  padding: 8px;
  width: 100%;
  background-color: rgba($color: #000000, $alpha: 0.4);
  font-size: 12px;
  border-radius: 8px;
}
.lxBox {
  background-color: #f6f6f6;
  margin-top: 10px;
  padding: 12px 17px;
  .name {
    align-items: center;
    font-size: 15px;
    color: #333333;
    font-weight: 500;
    margin-bottom: 8px;
    .line {
      width: 2px;
      height: 12px;
      margin-right: 5px;
      background-color: #eb6027;
    }
  }
}
.h-10 {
  height: 10px;
}
/deep/ .font1 {
  width: 308px;
}
.popupText {
  font-size: 14px;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #f6f6f6;
}
.yw-box {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  .item {
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
      width: 40px;
      height: 40px;
      margin-bottom: 10px;
    }
    span {
      font-size: 13px;
      color: #333;
      white-space: nowrap;
    }
  }
}
.ry-box1{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  .item {
    display: flex;
    flex-direction: column;
    border:1px solid #F8F8F8;
    border-radius:4px;
    align-items: center;
    img {
      width: 60px;
      height: 60px;
      margin-bottom: 10px;
    }
    span {
      font-size: 13px;
      color: #333;
      text-align:center;

    }
  }
}
</style>



