
<template>
  <div class="d-flex title " :style="{'justify-content':float,'color':color}"  @click="getTo">
      {{ title }}
      <van-icon :name="icon" :size="sizeIcon"></van-icon>
  </div>
</template>

<script>
export default {
  props:{
    title:{
      type:String,
      default:'更多'
    },
    float:{
      type:String,
      default:'flex-end'
    },
    icon:{
      type:String,
      default:'arrow'
    },
    path:{
      type:String,
      default:''
    },
    sizeIcon:{
      type:String,
      default:'12px'
    },
    color:{
      type:String,
      default:'#000000'
    },
    query:{
      type: Object,
      default: null,
    }
  },
  data(){
    return{

    }
  },
  methods:{
    getTo(){
      // console.log(this.query,this.path)
      if(!this.path) return
      this.$router.push({
        path:this.path,
        query: this.query?this.query:{},
      });
    }
  }
}
</script>

<style lang="scss" scoped>
.title{
  font-size:12px;
  color:#000000;
  font-weight: 400;
}
</style>



